/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  background: #002222;
  margin: 0;
}

@font-face {
  font-family: "Pacifico";
  font-style: normal;
  font-weight: 400;
  src: local("Pacifico Regular"), local("Pacifico-Regular"), url(https://fonts.gstatic.com/s/pacifico/v12/FwZY7-Qmy14u9lezJ-6H6MmBp0u-.woff2) format("woff2");
  font-display: swap;
}
section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
}
@media (min-width: 1007px) {
  section {
    justify-content: center;
    height: 100vh;
  }
}

#about,
#skills,
#work {
  min-height: 100vh;
}

.container {
  position: relative;
  width: 95%;
  height: 100%;
  max-width: 240rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 640px) {
  .container {
    row-gap: 1em;
  }
}
@media (min-width: 1007px) {
  .container {
    flex-direction: row;
    align-items: center;
  }
}

a {
  font-family: "Bebas Neue", sans-serif;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
}

p {
  font-family: "Lato", sans-serif;
  font-size: 1.5rem;
  margin: 0;
}

label {
  font-family: "Lato", sans-serif;
  color: #FFF0D3;
}

h1,
h2,
h3,
h4 {
  font-family: "Lato", sans-serif;
  margin: 10em;
  color: black;
  margin: 0;
  font-weight: 500;
  text-transform: uppercase;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 2.8rem;
}

h3 {
  font-size: 2.4rem;
}

h4 {
  font-size: 1.8rem;
  margin: 0;
}

#about,
#work {
  background-color: #FFF0D3;
}

.personal-links {
  position: absolute;
  display: flex;
  z-index: 1;
}
.personal-links img {
  width: 9vw;
  max-width: 4rem;
}
@media (min-width: 1007px) {
  .personal-links img {
    width: 3vw;
    min-width: 5rem;
  }
}

.bg-img {
  max-width: 100%;
  height: 100vh;
}

.item-unvisible {
  visibility: hidden;
}

.item-visible {
  visibility: visible;
}

.text {
  text-align: justify;
  line-height: 1.8;
  padding: 2rem;
}
.text h4 {
  color: #000000;
  font-size: 0.95em;
}
@media (min-width: 1007px) {
  .text {
    width: 80%;
    height: auto;
    margin: 0.5rem 1.5rem;
    margin-right: 3rem;
    padding: 0 5rem;
    text-align: justify;
  }
  .text h4 {
    font-size: 1.3em;
  }
}

header {
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 101;
  overflow: hidden;
}

header .container {
  width: 100%;
}

.navbar-logo {
  animation: navbar-logo 2s cubic-bezier(0.5, 0.6, 0.6, 1);
  animation-fill-mode: backwards;
}
.navbar-logo h1 {
  font-family: "Bebas Neue", sans-serif;
  font-size: 6rem;
  color: #7f1a35;
  transition: cubic-bezier(0.8, 1, 0.8, 1) 1s;
}
.navbar-logo h1 span {
  color: #FFF0D3;
}
.navbar-logo h2 {
  padding-left: 1.3rem;
  margin-top: -4.7rem;
  text-align: center;
  font-family: "Bebas Neue", sans-serif;
  font-size: 3rem;
  color: #7f1a35;
  transition: cubic-bezier(0.8, 1, 0.8, 1) 1s;
}
.navbar-logo h2 span {
  color: #FFF0D3;
}
@media (min-width: 1007px) {
  .navbar-logo h1 {
    font-size: 6em;
    transition: cubic-bezier(0.8, 1, 0.8, 1) 1s;
  }
  .navbar-logo h1 span {
    color: #FFF0D3;
  }
  .navbar-logo h2 {
    padding-left: 2.5rem;
    margin-top: -8rem;
    text-align: center;
    font-family: "Bebas Neue", sans-serif;
    font-size: 3em;
    color: #7f1a35;
    transition: cubic-bezier(0.8, 1, 0.8, 1) 1s;
  }
  .navbar-logo h2 span {
    color: #FFF0D3;
  }
}

@keyframes navbar-logo {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.checkbox {
  position: absolute;
  display: block;
  height: 32px;
  width: 32px;
  top: 12vh;
  left: 45vw;
  z-index: 5;
  opacity: 0;
  cursor: pointer;
}
@media (min-width: 1007px) {
  .checkbox {
    display: none;
  }
}

.hamburger-lines {
  display: block;
  height: 26px;
  width: 32px;
  margin: 1rem 0;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 1007px) {
  .hamburger-lines {
    display: none;
  }
}

.line {
  display: block;
  height: 4px;
  width: 100%;
  border-radius: 1rem;
  background: #dfe1e2;
}
@media (min-width: 1007px) {
  .line {
    display: none;
  }
}

.line1 {
  transform-origin: 0% 0%;
  transition: transform 0.4s ease-in-out;
}

.line2 {
  transition: transform 0.2s ease-in-out;
}

.line3 {
  transform-origin: 0% 100%;
  transition: transform 0.4s ease-in-out;
}

.navbar-menu {
  display: none;
  padding-top: 9rem;
  background-color: #002222;
  height: 100vh;
  width: 100%;
  margin-top: 1rem;
  flex-direction: column;
  text-align: center;
}
.navbar-menu a {
  color: #FFF0D3;
  padding: 0rem 2rem;
  font-size: 3rem;
}
@media (min-width: 1007px) {
  .navbar-menu {
    padding: 0;
    box-shadow: none;
    display: flex;
    position: fixed;
    height: initial;
    margin: 0;
    transition: none;
    transform: none;
    top: 85vh;
    flex-direction: row;
    justify-content: center;
    background-color: unset;
    transition: cubic-bezier(0.8, 1, 0.8, 1) 0.35s;
  }
  .navbar-menu a {
    padding: 0.45rem 2.5rem 0 2.5rem;
    font-size: 5vw;
    transition: cubic-bezier(0.8, 1, 0.8, 1) 0.8s;
  }
  .navbar-menu .active {
    background-color: #005555;
  }
  .navbar-menu a:hover {
    background-color: rgba(0, 0, 0, 0.6);
  }
}

@keyframes navbar-menu {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.visibleMenu {
  display: flex;
}

.lineAnimation .line1 {
  transform: rotate(45deg);
}
.lineAnimation .line2 {
  transform: scaleY(0);
}
.lineAnimation .line3 {
  transform: rotate(-45deg);
}

.move-header {
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.75);
}
@media (min-width: 1007px) {
  .move-header {
    padding: 0 !important;
  }
}
.move-header .navbar-logo h1 {
  font-size: 3.5rem;
  transition: cubic-bezier(0.5, 0.3, 0.3, 0.8) 1.4s;
}
.move-header .navbar-logo h2 {
  padding-left: 1.1rem;
  margin-top: -2.8rem;
  font-size: 1.7rem;
  transition: cubic-bezier(0.5, 0.3, 0.3, 0.8) 1.4s;
}
@media (min-width: 1007px) {
  .move-header .navbar-logo h1 {
    font-size: 3em;
    transition: cubic-bezier(0.5, 0.3, 0.3, 0.8) 1.4s;
  }
  .move-header .navbar-logo h2 {
    padding-left: 1.13rem;
    margin-top: -4rem;
    font-size: 1.5em;
    transition: cubic-bezier(0.5, 0.3, 0.3, 0.8) 1.4s;
  }
}
@media (min-width: 1007px) {
  .move-header .navbar-menu {
    top: 90%;
    transition: cubic-bezier(0.8, 1, 0.8, 1) 1.5s;
  }
  .move-header .navbar-menu a {
    font-size: 2.2vw;
  }
}

@media (min-width: 1007px) {
  .change-color a {
    color: #7f1a35;
  }
  .change-color a:hover {
    background-color: rgba(45, 0, 0, 0.8);
    color: #FFF0D3;
  }
  .change-color .active {
    color: #FFF0D3;
    background-color: #7f1a35;
  }
}

#home {
  min-height: 100vh;
}

.personal-img {
  z-index: -1;
  width: 100%;
  height: 100vh;
  position: fixed;
  display: flex;
  justify-content: center;
  animation: personal-img 1.2s cubic-bezier(0.5, 0.6, 0.6, 1);
  animation-fill-mode: backwards;
}
.personal-img img {
  position: absolute;
  bottom: 0%;
  width: 100%;
  height: auto;
}
@media (min-width: 640px) {
  .personal-img img {
    width: 80%;
  }
}
@media (min-width: 1007px) {
  .personal-img img {
    width: 60rem;
  }
}

@keyframes personal-img {
  0% {
    opacity: 0;
    transform: scale(1.1);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.move-img {
  position: absolute;
}

.about-graph {
  display: flex;
  width: 100%;
  height: 35vh;
  justify-content: center;
  margin-bottom: -5rem;
}
.about-graph img {
  width: auto;
  height: 70%;
}
@media (min-width: 1007px) {
  .about-graph {
    align-items: center;
    width: 80vw;
    height: 50vh;
    margin: 0 1rem;
  }
  .about-graph img {
    width: auto;
    height: 60%;
  }
}

.social {
  top: 95%;
  column-gap: 3rem;
}
.social img {
  width: 100%;
  height: auto;
}
@media (min-width: 1007px) {
  .social {
    top: 70%;
    column-gap: 5rem;
  }
}

#work .text p {
  height: 20rem;
}

.swiper {
  width: 85%;
  height: 30vh;
}
@media (min-width: 1007px) {
  .swiper {
    width: 40%;
    height: 55%;
  }
}

.swiper-slide {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 22px;
  font-weight: bold;
  color: #FFF0D3;
  cursor: grab;
}
.swiper-slide img {
  width: 100%;
  height: 100%;
}
.swiper-slide:hover .hover-link {
  visibility: visible;
  width: 100%;
  height: 20%;
  background-color: rgba(0, 85, 85, 0.8);
}
.swiper-slide:hover .hover-link a {
  visibility: visible;
  padding: 0.2rem 4.5rem;
}

.hover-link {
  visibility: hidden;
  position: absolute;
  width: 100%;
  height: 0%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s ease-in-out;
  text-align: center;
}
.hover-link a {
  font-family: "Lato", sans-serif;
  font-size: 1.3rem;
  visibility: hidden;
  color: #005555;
  border-radius: 1rem;
  background-color: #FFF0D3;
  transition: 0.3s ease-in-out;
}
.hover-link:hover {
  cursor: pointer;
}
@media (min-width: 1007px) {
  .hover-link a {
    font-size: 1.8rem;
  }
}

.github {
  margin-bottom: 1rem;
}
.github img {
  width: auto;
  height: 6vh;
  min-height: 4rem;
}
@media (min-width: 1007px) {
  .github {
    position: absolute;
    top: 70%;
  }
}

#skills .container {
  margin: 5rem 0;
}
#skills .container h3 {
  color: #FFF0D3;
  text-align: center;
}
#skills .container h4 {
  color: #FFF0D3;
  margin-bottom: 2rem;
  text-align: center;
}
#skills .container p {
  color: #FFF0D3;
}
@media (min-width: 1007px) {
  #skills .container {
    flex-direction: column;
    row-gap: 5rem;
    margin-top: -4rem;
  }
  #skills .container h4 {
    margin-bottom: 3rem;
  }
}

.tools {
  width: 75%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 2.5rem;
}
@media (min-width: 1007px) {
  .tools {
    row-gap: 5rem;
    width: 100%;
  }
}

.col {
  display: flex;
  justify-content: center;
  column-gap: 5rem;
  height: auto;
}
.col img {
  width: auto;
  height: auto;
  max-height: 4rem;
}
@media (min-width: 1007px) {
  .col img {
    height: auto;
    max-height: 6rem;
  }
}

#contact {
  height: 130vh;
  max-height: 95rem;
}
@media (min-width: 1007px) {
  #contact {
    min-height: 100vh;
  }
}

#contact .container {
  height: 130vh;
  width: 100%;
  row-gap: 2.5rem;
}
@media (min-width: 1007px) {
  #contact .container {
    height: 100%;
    width: 90%;
    column-gap: 5rem;
  }
}

.map {
  width: 100%;
  height: 30%;
}
.map img {
  width: 100%;
  height: 100%;
}
@media (min-width: 1007px) {
  .map {
    position: relative;
    width: 50%;
    height: 65%;
  }
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  row-gap: 1rem;
  height: 70%;
}
@media (min-width: 1007px) {
  .form {
    justify-content: center;
  }
}

.group {
  width: 80vw;
  display: flex;
  flex-direction: column;
}
.group h3 {
  margin-bottom: 1rem;
  color: #FFF0D3;
}
.group input,
.group textarea {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  color: #FFF0D3;
  background-color: transparent;
  width: 100%;
  height: 4rem;
  padding: 0.5rem;
  border: none;
  outline: none;
  border-bottom: 1px solid #FFF0D3;
}
.group textarea {
  border: 1px solid #FFF0D3;
  padding: 1rem 0.5rem;
  margin-top: 0.7rem;
  resize: none;
  height: 15vh;
  border-radius: 0.5rem;
}
@media (min-width: 1007px) {
  .group {
    width: 35vw;
  }
  .group input,
.group textarea {
    width: 100%;
  }
}

.error-text {
  min-height: 3rem;
  width: 100%;
  opacity: 0;
  margin-top: 0.5rem;
  color: #005555;
  transition: opacity 0.4s cubic-bezier(0, 0.1, 0.1, 0.5);
}
@media (min-width: 1007px) {
  .error-text {
    width: 75%;
  }
}

.btn-form {
  margin: 0;
  width: 100%;
  height: 5rem;
  color: #002222;
  font-family: "Lato", sans-serif;
  font-size: 1.5rem;
  background-color: #FFF0D3;
  cursor: pointer;
  transition: 0.5s ease-in-out;
  border: none;
}
.btn-form:hover {
  border-radius: 10rem;
  background-color: #005555;
  color: #FFF0D3;
}

footer {
  position: absolute;
  bottom: 0%;
  width: 100%;
  text-align: center;
}
footer p {
  color: #FFF0D3;
}